* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.search {
  background-color: #00f;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.container {
  max-width: 1480px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  padding: 0;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border-radius: 4px;
  flex-basis: calc(25% - 10px);
  padding: 10px;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.load-more {
  min-width: 200px;
  height: 50px;
  letter-spacing: .06em;
  text-transform: none;
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

